import React from 'react';
											  
import AppBar from '@material-ui/core/AppBar';
import { useHistory } from "react-router-dom";
													  
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Divider, Drawer, Hidden, IconButton, Toolbar, List, ListItem, ListItemIcon, ListItemText } from '@material-ui/core';
													   
import { Menu as MenuIcon } from '@material-ui/icons';
import categories from '../categories'

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex'
  },
  drawer: {
    [theme.breakpoints.up('md')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    backgroundColor: 'white'
  },
  menuButton: {
    marginRight: theme.spacing(1),
    marginLeft: theme.spacing(1),
    color: "black",
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
    border: "unset"
  },
  content: {
    flexGrow: 1,
    paddingTop: theme.spacing(1),
  },
  listItem: {
    textAlign: 'right',
    borderRadius: theme.spacing(4) + "px 0px 0px "+ theme.spacing(4)+"px",
  },
  divider: {
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2), 
  },
  highlitedItem: {
    fontWeight: "600"
  },
  nonHighlightedItem : {
    fontWeight: "100",
  }
}));

function ResponsiveDrawerLayout(props) {
  const { window, match } = props;
  const selectedCategory = props.category || categories[match?.params?.category]?.title || categories.latest.title;
  const classes = useStyles();
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const history = useHistory();

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };
  const gotoRoute = (category) => {
    if(mobileOpen) {
      handleDrawerToggle();
    }
    history.push(`/${category}`);
  };
  
  const getMenuItems = (categoriesList) => (
    <List>
        {categoriesList.map((category) => (
          <ListItem button key={categories[category].displayName} className={classes.listItem}
            style={category === selectedCategory ? {backgroundColor : categories[category].highlightColor} : {}}
            onClick={()=> gotoRoute(category)}
          >
            <ListItemIcon style= {category === selectedCategory? {color : categories[category].color }:{}}>{categories[category].icon}</ListItemIcon>
            <ListItemText primaryTypographyProps={{className : (category === selectedCategory? classes.highlitedItem : classes.nonHighlightedItem)}}
                 style= {category === selectedCategory ?{color : categories[category].color }:{}} primary={categories[category].displayName} />
          </ListItem>
        ))}
      </List>
  )

  const drawer = (
    <div>
      <div className={classes.toolbar} />
      <Divider className={classes.divider}/>
        {getMenuItems(categories.otherList)}
      <Divider className={classes.divider}/>
      <List>
      {getMenuItems(categories.list)}
      </List>
    </div>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <div className={classes.root}>
      
      <AppBar elevation="2" position="fixed" className={classes.appBar}>
        <Toolbar style={{padding: 0}}>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
          <img className={classes.toolbar} src={process.env.PUBLIC_URL + 'logo.png'} alt="Logo"
            style={{maxHeight: 64,     marginRight: theme.spacing(2) }}
            onClick={()=>gotoRoute('')}
          />

        </Toolbar>
      </AppBar>
			 
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden mdUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden smDown className="test" implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
      <main className={classes.content}>
        
      <div className={classes.toolbar} />
            {props.children}
      </main>
    </div>
  );
}

export default ResponsiveDrawerLayout;
